.App {
  display: inline-flex;
  flex-flow: column nowrap;
  padding: 24px;
}

.imgWrapper {
  padding: 16px;
  position: relative;
  width: 496px;
  height: 673px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url('./WechatIMG3403.png');

}

.form {
  margin-top: 24px !important;
}

.mask {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  background: rgb(81, 46, 168);
  position: absolute;
  bottom: 50px;
  left: 54px;
  width: 397px;
  height: 420px;
}

.img {
  margin-top: 8px;
  width: 248px;
  height: 311px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.job, .name {
  color: #fff;
  margin-top: 10px;
}

.job {
  font-size: 16px;
}

.name {
  font-size: 24px;
}